var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('GenericView',{attrs:{"title":"Sobre"}},[_c('div',{staticClass:"content"},[_c('h2',[_vm._v("PROJETO ECODOMO")]),_c('p',[_vm._v("Em meio a situação climática e social do semiárido,\n      surge oProjeto Ecolume - projeto de pesquisa baseado\n      no modelo de economia circular com agricultura de baixo\n      carbono, e que busca a sinergia entre as seguranças\n      hídrica, energética e alimentar da região. Nas palavras\n      de Francis Lacerda, coordenadora do projeto:\n      “O conceito Ecolume visa apresentar provas materiais\n      de que é possível plantar água,\n      “comer Caatinga, e irrigá-la com o sol”.\n      "),_c('br'),_c('br'),_vm._v("\n      O projeto se baseia no modelo agro\n      voltaico, e uma das ideias é a construção\n      de cúpulas geodésicas com sistemas de captação de água\n      da chuva, onde haverá o cultivo de mudas de espécies\n      nativas da Caatinga, que deverão ser replantadas em seu\n      habitat natural. A educação foi o meio escolhido pelo Ecolume\n      para chegar nos agricultores, por isso as cúpulas serão\n      construídas em escolas e universidades.\n      O projeto hoje está em execução no Serviço de\n      Tecnologia Alternativa (SERTA) de Ibimirim,\n      lá os alunos são ensinados a trabalhar com esse sistema\n      para que possam atuar na transformação\n      das circunstâncias econômicas, sociais, ambientais, culturais\n      e políticas, na promoção do desenvolvimento sustentável com\n      foco no campo, disseminando esse conhecimento para suas famílias\n      e toda a comunidade.\n      "),_c('br'),_c('br'),_vm._v("\n      Dentro desse contexto, o desafio proposto para o\n      L.O.U.Co e sua equipe foi implementar IoT (Internet of Things)\n      no projeto, para monitorar e compartilhar dados\n      e indicadores. Nas primeiras semanas, nos adiantamos\n       na definição do problema, que enunciamos da seguinte forma:\n      “A falta de monitoramento acarreta a perda de dados vitais ao\n      plantio”, no entanto, com um aprofundamento nas pesquisas,\n      constatamos que estabelecer um sistema de monitoramento\n      não é o desafio maior imediato, pois existe um viés educativo\n      e a intenção de fazer uma sala de situação dentro do Instituto\n      Agronômico de Pernambuco (IPA). Por conta disso, decidimos\n      que iríamos desenvolver uma plataforma que fosse educativa\n      e engajasse os jovens inseridos na realidade do semi-árido\n      com o conceito Ecolume para solucionar o seguinte problema: “Como podemos\n      fazer essa implementação com o foco em pessoas, de forma\n      que eduque e engaje com o conceito Ecolume e ao mesmo tempo\n      seja facilmente replicável?\" Mas após estruturarmos o cronograma,\n      constatamos que esse problema seria inviável de ser solucionado\n      para o período de tempo e ferramentas disponíveis, então nós\n      nos reunimos com as partes interessadas do projeto e\n      alinhamos que nossa solução obteria dados e os exibiria\n      em uma interface adequada a pesquisadores\n      e pessoas interessadas em mudanças climáticas\n      e/ou agricultura no semi-árido, deixando para o\n      Ecolume executar a parte educativa utilizando esses dados coletados.")]),_c('img',{staticClass:"photo",attrs:{"src":"http://i67.tinypic.com/25oxh0z.jpg"}}),_c('iframe',{attrs:{"width":"1020","height":"500","src":"https://www.youtube.com/embed/GekEpxacBBg","frameborder":"0","allow":"accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":""}}),_c('div',[_c('p',[_vm._v("Responsáveis pelo projeto:"),_c('br'),_vm._v("\n        - Anna Gabriela Carvalho (agmc@cesar.school)"),_c('br'),_vm._v("\n        - Antonio Almeida (aaba@cesar.school)"),_c('br'),_vm._v("\n        - Gabriel Lapa (gbvl@cesar.school)"),_c('br'),_vm._v("\n        - Giovanni Procida (gbvl@cesar.school)"),_c('br'),_vm._v("\n        - Henrique Cruz (hjfcn@cesar.school)"),_c('br'),_vm._v("\n        - Maria Eduarda Espíndola (hjfcn@cesar.school)"),_c('br'),_vm._v("\n        - Tiago Jales (tpj@cesar.school)"),_c('br'),_c('br'),_c('b',[_vm._v("Email para contato:")]),_vm._v(" ecodomo.projeto@gmail.com\n      ")])])])])}
